import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-account-subscription',
  templateUrl: './account-subscription.component.html',
  styleUrls: ['./account-subscription.component.scss'],
})
export class AccountSubscriptionComponent implements OnInit {
  @Input() name: string;
  @Input() cost: number;
  @Input() pages: number;
  @Input() additionalCost: string;
  @Input() selected: boolean;

  @Output() changed = new EventEmitter<boolean>();

  constructor() {}

  ngOnInit(): void {}

  onSelected = (): void => {
    this.changed.next(this.selected);
  };
}
