import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, MetaDataType } from 'shared-code-lib';
import { AccountRegistrationDetails, FieldState } from '../../../../blocks/InterFAX/Models/admin';
import { NewAccount } from '../account-create/account-create.component';

@Component({
  selector: 'app-account-addresses',
  templateUrl: './account-addresses.component.html',
  styleUrls: ['./account-addresses.component.scss'],
})
export class AccountAddressesComponent implements OnInit {
  @Input() account: NewAccount;
  @Input() accountRegistrationDefaults: AccountRegistrationDetails;

  @Output() valid = new EventEmitter<boolean>();

  fieldCompanyName: FieldDefinition;
  fieldName: FieldDefinition;
  fieldPhoneNumber: FieldDefinition;
  fieldTaxId: FieldDefinition;

  constructor(private translateService: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
    this.updateValidity();
  }

  updateValidity = (): void => {
    let valid = true;

    if (!this.account.name) valid = false;
    if (!this.account.shipping.address1) valid = false;
    if (!this.account.shipping.city) valid = false;
    if (!this.account.shipping.postalCode) valid = false;
    if (!this.account.phoneNumber) valid = false;

    if (this.accountRegistrationDefaults.displayTaxId == FieldState.Required && !this.account.taxid) valid = false;

    this.valid.next(valid);
  };

  private createFields = () => {
    this.fieldCompanyName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('account-register.company-name'),
      placeholder: '',
      required: false,
    });

    this.fieldName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('account-register.name'),
      placeholder: '',
      required: true,
    });

    this.fieldPhoneNumber = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('account-register.phone-number'),
      placeholder: '',
      required: true,
    });

    this.fieldTaxId = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('account-register.taxid'),
      placeholder: '',
      required: this.accountRegistrationDefaults.displayTaxId == FieldState.Required,
    });
  };

  fieldChanged = (): void => {
    this.updateValidity();
  };
}
