import { Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { BrandingService, FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import { CountryZones, AccountRegistrationDetails, PrePaidPart } from '../../../../blocks/InterFAX/Models/admin';
import { Countries } from '../../../../blocks/InterFAX/Models/outbound';
import { NewAccount } from '../account-create/account-create.component';

@Component({
  selector: 'app-account-services',
  templateUrl: './account-services.component.html',
  styleUrls: ['./account-services.component.scss'],
})
export class AccountServicesComponent implements OnInit, OnChanges {
  @Input() account: NewAccount;
  private _accountRegistrationDefaults: AccountRegistrationDetails;
  @Input()
  public set accountRegistrationDefaults(accountRegistrationDefaults: AccountRegistrationDetails) {
    this._accountRegistrationDefaults = accountRegistrationDefaults;
    this.countryZones = this._accountRegistrationDefaults.countryZones;
    if (this.zoneDataSource) this.loadZoneDataSource(this.account.countryCode);
    if (this.zoneDataSource) this.setInBoundValues();

    if (this.zone) this.onCountryLocationChanged(this.zone);
  }

  public get accountRegistrationDefaults(): AccountRegistrationDetails {
    return this._accountRegistrationDefaults;
  }

  @Output() valid = new EventEmitter<boolean>();
  @Output() countryChanged = new EventEmitter<string>();

  countryZones = new Array<CountryZones>();

  fieldInboundCountryForFaxNumber: FieldDefinition;
  fieldInboundCountryLocation: FieldDefinition;
  fieldAccountCountry: FieldDefinition;

  zoneDataSource: FixedDataSource;

  zone: string;

  packageType = 1;
  subscriptionType = -1;
  contactUsUrl: string;

  constructor(private translateService: TranslateService, private brandingService: BrandingService) { }

  ngOnInit(): void {
    this.zoneDataSource = new FixedDataSource();
    this.loadZoneDataSource(this.account.countryCode);

    this.createFields();

    const selectedPart = this.accountRegistrationDefaults.outboundPricing.prePaidParts.find(
      (ppp) => ppp.basePart.number == this.account.outboundType
    );
    if (selectedPart) this.onSelectOutboundPart(selectedPart);

    this.setInBoundValues();

    this.updateValidity();

    this.contactUsUrl = this.brandingService.getBrandDetails()?.contactUsUrl;
  }

  ngOnChanges(): void {
    this.updateValidity();
  }

  updateValidity = (): void => {
    let valid = true;

    if (this.packageType == 0) {
    } else if (this.packageType == 1) {
      if (this.account.outbound.enabled == false && this.account.inbound.enabled == false) valid = false;

      if (this.account.outbound.enabled) {
        if (this.account.outboundType == -1) valid = false;
      }

      if (this.account.inbound.enabled) {
        if (!this.account.inbound.countryCode) {
          valid = false;
          this.zone = '';
        }

        if (!this.zone) valid = false;
      }
    }

    this.valid.next(valid);
  };

  onSelectOutboundPart(part: PrePaidPart) {
    this.account.outboundType = part.basePart.number;
    this.account.outbound.setupCharge = ''; //tbd
    this.account.outbound.subscriptionCharge = part.basePart.displayCost;

    this.updateValidity();
  }

  onAccountCountryCodeChanged = (countryCode: string) => {
    this.countryChanged.next(countryCode);
    this.updateValidity();
  };

  onCountryLocationChanged = (zoneNode: string) => {
    this.updateValidity();

    const countryZone = this.countryZones.find((cz) => cz.countryCode == this.account.inbound.countryCode);
    const zone = countryZone.zones.find((z) => z.value == zoneNode);

    this.account.inbound.IPVNodeId = zone.node.id;
    this.account.inbound.prefix = zone.prefix;
    this.account.inbound.setupCharge = zone.node.setPrice;
    this.account.inbound.subscriptionCharge = zone.node.subPrice;
    this.account.inbound.faxnumberDetails = `${countryZone.country}-${zone.name}`;
  };

  onCountryCodeChanged = (countryCode: string) => {
    this.loadZoneDataSource(countryCode);

    if (this.zoneDataSource.options.length > 0) {
      this.zone = this.zoneDataSource.options[0].value.toString();
      this.onCountryLocationChanged(this.zone);
    }

    this.updateValidity();
  };

  onOutboundEnabledChanged = (enabled: boolean): void => {
    this.account.outbound.enabled = enabled;

    this.updateValidity();
  };

  onSubscription = (type: number, enabled: boolean): void => {
    this.subscriptionType = type;
    this.updateValidity();
  };

  onPackageType = (type: number): void => {
    this.packageType = type;
    this.updateValidity();
  };

  onInboundEnabledChanged = (enabled: boolean): void => {
    this.account.inbound.enabled = enabled;
    this.updateValidity();
  };

  createFields = (): void => {
    const options = Countries.getAsFieldOptions(this.accountRegistrationDefaults.countries, this.translateService);
    const countryDataSource = new FixedDataSource(options);
    this.fieldAccountCountry = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('account-register.account-country'),
      placeholder: '',
      required: true,
      datasource: countryDataSource,
    });

    this.fieldInboundCountryForFaxNumber = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('account-register.inbound-country-for-faxnumber'),
      placeholder: '',
      required: true,
      datasource: this.getCountriesDataSource(),
    });

    this.fieldInboundCountryLocation = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('account-register.inbound-country-for-faxnumber-location'),
      placeholder: '',
      required: true,
      datasource: this.zoneDataSource,
    });
  };

  getCountriesDataSource = (): FixedDataSource => {
    const options = new Array<IFieldOptions>();

    for (const cz of this.countryZones) {
      options.push({
        name: this.translateService.instant(`Countries.${cz.country.replace(/[^a-zA-Z0-9]/g, '')}`),
        value: cz.countryCode,
      });
    }

    options.sort((x, y) => x.name.localeCompare(y.name));
    return new FixedDataSource(options);
  };

  loadZoneDataSource = (countryCode: string): void => {
    const options = new Array<IFieldOptions>();

    const countryZone = this.countryZones.find((cz) => cz.countryCode == countryCode);
    if (countryZone) {
      for (const zone of countryZone.zones) {
        const zoneNameKey = `Zones.${zone.name}`;
        const translatedZoneName = this.translateService.instant(zoneNameKey);
        options.push({ name: zoneNameKey == translatedZoneName ? zone.name : translatedZoneName, value: zone.value });
      }
    }

    options.sort((x, y) => x.name.localeCompare(y.name));

    this.zoneDataSource.updateOptions(options);
  };

  setInBoundValues = (): void => {
    if (this.account.inbound.enabled) {
      const faxDetails = this.account.inbound.faxnumberDetails.split('-');
      if (faxDetails.length > 0) {
        const country = faxDetails[0];
        const countryZone = this.countryZones.find((cz) => cz.country == country);
        this.account.inbound.countryCode = countryZone.countryCode;
        this.loadZoneDataSource(this.account.inbound.countryCode);
        this.zone = countryZone.zones.find(
          (z) => z.node.id == this.account.inbound.IPVNodeId && z.prefix == this.account.inbound.prefix
        ).value;
      }
    }
  };
}
