import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType, PasswordPolicy } from 'shared-code-lib';
import { AccountRegistrationDetails } from '../../../../blocks/InterFAX/Models/admin';
import { InterFAXService } from '../../../../blocks/services/interfax.service';
import { SpinnerStateService } from '../../../../blocks/services/spinner-state.service';
import { NewAccount } from '../account-create/account-create.component';

@Component({
  selector: 'app-account-details',
  templateUrl: './account-details.component.html',
  styleUrls: ['./account-details.component.scss'],
})
export class AccountDetailsComponent implements OnInit {
  @Input() account: NewAccount;
  @Input() accountRegistrationDefaults: AccountRegistrationDetails;

  @Output() valid = new EventEmitter<boolean>();

  fieldUsername: FieldDefinition;
  fieldEmail: FieldDefinition;
  fieldPassword: FieldDefinition;
  fieldConfirmPassword: FieldDefinition;
  fieldTimeZone: FieldDefinition;
  options = new Array<IFieldOptions>();
  passwordPolicy: PasswordPolicy;

  constructor(
    private translateService: TranslateService,
    private spinnerService: SpinnerStateService,
    private toastr: ToastrService,
    private interFaxService: InterFAXService
  ) {}

  ngOnInit(): void {
    this.createFields();

    if (!this.options.find((x) => x.name == this.account.timeZone)) {
      const countryTimeZones = this.accountRegistrationDefaults.countries.find(
        (c) => c.code == this.account.countryCode
      );
      if (countryTimeZones && countryTimeZones.timeZones.length > 0)
        this.account.timeZone = countryTimeZones.timeZones[0];
    }

    this.updateValidity();

    this.spinnerService.show(
      this.translateService.instant('MainHeader.loading-password-requirements'),
      document.documentElement
    );
    this.interFaxService.getInterFaxPasswordPolicy(this.passwordPolicyLoaded, this.passwordPolicyLoadFailed);
  }

  updateValidity = (): void => {
    let valid = true;

    if (!this.account.username?.trim()) valid = false;
    if (!this.account.email?.trim()) valid = false;
    if (!this.account.password?.trim()) valid = false;
    if (!this.account.confirmPassword?.trim()) valid = false;
    if (!this.account.timeZone?.trim()) valid = false;

    if (this.account.password?.trim() != this.account.confirmPassword?.trim()) valid = false; // todo: Probably need error msg?

    this.valid.next(valid);
  };

  passwordPolicyLoaded = (pp: PasswordPolicy): void => {
    this.passwordPolicy = pp;
    this.spinnerService.hide();
  };

  passwordPolicyLoadFailed = () => {
    this.toastr.error(this.translateService.instant('MainHeader.failed-to-load-password-requirements'));
    this.spinnerService.hide();
  };

  fieldChanged = (_: string): void => {
    this.updateValidity();
  };

  checkAvailability = (): void => {
    //tbd
  };

  private createFields = () => {
    this.fieldUsername = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-user-properties.username'),
      placeholder: '',
      required: true,
    });

    this.fieldEmail = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('fax-user-properties.email'),
      placeholder: '',
      required: true,
    });

    this.fieldPassword = new FieldDefinition({
      type: MetaDataType.Password,
      label: this.translateService.instant('account-register.password'),
      placeholder: '',
      required: true,
    });

    this.fieldConfirmPassword = new FieldDefinition({
      type: MetaDataType.Password,
      label: this.translateService.instant('account-register.confirm-password'),
      placeholder: '',
      required: true,
    });

    this.fieldTimeZone = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('fax-user-properties.timezone'),
      placeholder: '',
      required: false,
      datasource: this.getTimeZoneDataSource(),
    });
  };

  getTimeZoneDataSource = (): FixedDataSource => {
    const country = this.accountRegistrationDefaults.countries.find((c) => c.code == this.account.countryCode);
    if (country) {
      for (const tz of country.timeZones) {
        this.options.push({ name: tz, value: tz });
      }
    }

    return new FixedDataSource(this.options);
  };
}
