import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { AccountRegistrationDetails, IPurchaseSummary } from '../../../../blocks/InterFAX/Models/admin';
import { InterFAXService } from '../../../../blocks/services/interfax.service';
import { ISummaryProvider } from '../../account-components/parts-invoice/parts-invoice.component';
import { NewAccount } from '../account-create/account-create.component';

class SummaryProvider implements ISummaryProvider {
  constructor(private interfaxService: InterFAXService, private account: NewAccount) {}

  load(successCallBack: (id: IPurchaseSummary) => void, failureCallBack: (err: any) => void): void {
    this.interfaxService.getRegistrationPurchaseSummary(this.account, successCallBack, failureCallBack);
  }
}

@Component({
  selector: 'app-account-confirmation',
  templateUrl: './account-confirmation.component.html',
  styleUrls: ['./account-confirmation.component.scss'],
})
export class AccountConfirmationComponent implements OnInit {
  @Input() account: NewAccount;
  @Output() valid = new EventEmitter<boolean>();
  @Input() accountRegistrationDefaults: AccountRegistrationDetails;

  agreedToTerms = false;
  purchaseSummaryDisplayed = false;

  summaryProvider: ISummaryProvider;

  constructor(private translateService: TranslateService, private interfaxService: InterFAXService) {}

  ngOnInit(): void {
    this.updateValidity();
    this.summaryProvider = new SummaryProvider(this.interfaxService, this.account);
  }

  updateValidity = (): void => {
    let valid = true;

    if (!this.agreedToTerms) valid = false;
    if (!this.purchaseSummaryDisplayed) valid = false;

    this.valid.next(valid);
  };

  onDisplay = (): void => {
    this.purchaseSummaryDisplayed = true;
    this.updateValidity();
  };

  onAgreedToTermsChanged = (agreed: boolean): void => {
    this.agreedToTerms = agreed;
    this.updateValidity();
  };
}
