import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { AccountRegistrationDetails } from '../../../../blocks/InterFAX/Models/admin';
import { NewAccount } from '../account-create/account-create.component';
import {
  IRadioButtonOption,
  RadioButtonsLayout,
} from '../../../../../../../../Bamba.Shared/src/models/radio-buttons-group';
import { TranslateService } from '@ngx-translate/core';
import { PaymentMethods } from '../../../../../../../../Bamba.Shared/src/utils/enums';

@Component({
  selector: 'app-account-payment',
  templateUrl: './account-payment.component.html',
  styleUrls: ['./account-payment.component.scss'],
})
export class AccountPaymentComponent implements OnInit {
  @Input() account: NewAccount;
  @Input() accountRegistrationDefaults: AccountRegistrationDetails;

  @Output() valid = new EventEmitter<boolean>();

  ccEnabled: boolean;
  paypalEnabled: boolean;
  RadioButtonsLayout = RadioButtonsLayout;

  paymentOptions: IRadioButtonOption<string>[] = [];

  PaymentMethods = PaymentMethods;

  constructor(private translate: TranslateService) {}

  ngOnInit(): void {
    if (!this.account.billing.country) this.account.billing.country = this.account.shipping.country;

    this.updateValidity();

    if (this.paymentMethodEnabled(PaymentMethods.CC)) {
      this.ccEnabled = true;
      this.paymentOptions.push({
        label: this.translate.instant('payment-method.creditcard'),
        value: PaymentMethods.CC,
        icon: 'assets/img/credit-card.svg',
      });
    }

    if (this.paymentMethodEnabled(PaymentMethods.DD)) {
      this.paymentOptions.push({
        label: this.translate.instant('payment-method.directdebit'),
        value: PaymentMethods.DD,
        icon: 'assets/img/direct-debit.svg',
      });
    }

    if (this.paymentMethodEnabled(PaymentMethods.PayPal)) {
      this.paypalEnabled = true;
      this.paymentOptions.push({
        label: this.translate.instant('payment-method.paypal'),
        value: PaymentMethods.PayPal,
        icon: 'assets/img/paypal.svg',
      });
    }
  }

  paymentMethodEnabled = (paymentMethod: string): boolean => {
    return (
      this.accountRegistrationDefaults.country.paymentMeans.findIndex(
        (x) => x.toLowerCase() == paymentMethod.toLowerCase()
      ) != -1
    );
  };

  updateValidity = (): void => this.valid.next(this.account.isPaymentMethodValid());

  fieldChanged = (): void => {
    this.updateValidity();
  };
}
