import { Component, Input } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { AuthService, SessionStorageService } from 'shared-code-lib';
import { NewAccount } from '../account-create/account-create.component';
import { ApiResult } from './../../../../blocks/services/models';
import { SpinnerStateService } from './../../../../blocks/services/spinner-state.service';

@Component({
  selector: 'app-account-created',
  templateUrl: './account-created.component.html',
  styleUrls: ['./account-created.component.scss'],
})
export class AccountCreatedComponent {
  @Input() account: NewAccount;

  constructor(
    private router: Router,
    private sessionStorage: SessionStorageService,
    private authService: AuthService,
    private spinnerStateService: SpinnerStateService,
    private translateService: TranslateService,
    private toastr: ToastrService
  ) {}

  updateUserSessionAndGoHome = (): void => {
    this.spinnerStateService.show(
      this.translateService.instant('account-register.updating-user-session'),
      document.documentElement
    );

    this.authService.renewInterFaxUserSessionToken(this.sessionRenewed, this.requestFailed);
  };

  sessionRenewed = (result: ApiResult) => {
    this.spinnerStateService.hide();
    this.authService.startSession(result.object.token);
    this.sessionStorage.setReturnUrl('home');
    this.router.navigateByUrl('complete-user-info');
  };

  requestFailed = (err: any) => {
    this.toastr.error(err);
    this.spinnerStateService.hide();
  };
}
