import { ChangeDetectorRef, Component, ElementRef, isDevMode, OnInit, ViewChild } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { WizardBarPage } from '../../../../blocks/components/wizard-bar/wizard-bar.component';
import {
  Address,
  CountryZones,
  CreditCard,
  AccountRegistrationDetails,
  InboundService,
  OutboundService,
  AddressValidationResult,
  AccountAddresses,
} from '../../../../blocks/InterFAX/Models/admin';
import { SupportedValues } from '../../../../blocks/InterFAX/Models/outbound';
import { InterFAXService } from '../../../../blocks/services/interfax.service';
import { ApiResult, Control } from '../../../../blocks/services/models';
import { SpinnerStateService } from '../../../../blocks/services/spinner-state.service';
import { ErrorsService } from '../../../../blocks/services/errors.service';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { ActivatedRoute } from '@angular/router';
import { Log } from '../../../../blocks/core/log';
import { ToastrService } from 'ngx-toastr';
import { AuthorizationResult, AuthService, PaymentMethods, SessionStorageService } from 'shared-code-lib';
import { PaymentMethodConfigurations } from '../../../../blocks/InterFAX/Models/payment-methods';

export class NewAccount {
  countryCode: string;
  timeZone: string;

  email: string;
  username: string;
  password: string;
  confirmPassword: string;
  companyName: string;
  name: string;
  phoneNumber: string;
  taxid: string;

  billingSameAsShipping: boolean;
  billing: Address;
  shipping: Address;

  outboundType: number; //tbd

  inbound: InboundService;
  outbound: OutboundService;

  creditCard: CreditCard;
  paymentMethod: PaymentMethodConfigurations;

  constructor() {
    this.countryCode = '';
    this.timeZone = '';

    this.inbound = new InboundService();
    this.outbound = new OutboundService();

    this.outboundType = 0;

    this.email = '';
    this.companyName = '';
    this.username = '';
    this.password = '';
    this.confirmPassword = '';
    this.name = '';
    this.phoneNumber = '';

    this.billingSameAsShipping = true;
    this.billing = new Address();
    this.shipping = new Address();

    this.creditCard = new CreditCard();

    this.paymentMethod = new PaymentMethodConfigurations(PaymentMethods.CC);
  }

  isPaymentMethodValid(): boolean {
    if (!this.paymentMethod) return false;

    if (
      this.paymentMethod.name === PaymentMethods.CC &&
      (!this.creditCard.name ||
        !this.creditCard.cardType ||
        !this.creditCard.number ||
        !this.creditCard.expirationMonth ||
        !this.creditCard.expirationYear ||
        !this.creditCard.cvv)
    ) {
      return false;
    }

    if (!this.billingSameAsShipping) {
      if (!this.billing?.address1 || !this.billing?.city || !this.billing?.postalCode) return false;
    }

    return true;
  }
}

@Component({
  selector: 'app-account-create',
  templateUrl: './account-create.component.html',
  styleUrls: ['./account-create.component.scss'],
})
export class AccountCreateComponent implements OnInit {
  @ViewChild('spinnerWrapper', { static: true })
  spinnerWrapper: ElementRef;
  @ViewChild(PerfectScrollbarDirective) directiveRef?: PerfectScrollbarDirective;

  formControls: Array<Control> = [];

  account = new NewAccount();

  accountRegistrationDefaults: AccountRegistrationDetails;

  countryZones: Array<CountryZones>;

  supportedValues: SupportedValues;

  pages = new Array<WizardBarPage>();
  selectedPage = 0;

  selectedPageId = '';
  nextButtonText: string;

  show = false;

  valid = false;

  addressValidationResults: AddressValidationResult;

  registrationResult: ApiResult;

  constructor(
    private translateService: TranslateService,
    private spinnerService: SpinnerStateService,
    private interfaxService: InterFAXService,
    private errorsService: ErrorsService,
    private ngxSmartModalService: NgxSmartModalService,
    private cdr: ChangeDetectorRef,
    private authService: AuthService,
    private sessionStorageService: SessionStorageService,
    private toastr: ToastrService,
    private route: ActivatedRoute
  ) {}

  ngOnInit(): void {
    this.addPage('packages');
    this.addPage('account-details');
    this.addPage('billing-details');
    this.addPage('payment');
    this.addPage('confirmation');
    this.addPage('finish');

    this.selectedPageId = 'packages';

    this.loadAccountRegistrationDefaults('');

    if (isDevMode()) {
      //todo: ruddy Typing Saver
      this.account.username = 'joe-site-com';
      this.account.email = 'joe@site.com';
      this.account.password = 'testing1234!!';
      this.account.confirmPassword = 'testing1234!!';
      this.account.companyName = 'joes broken dreams';
      this.account.name = 'joe joesphson';
      this.account.phoneNumber = '1-978-555-1212';
      this.account.shipping.address1 = '5 Main street';
      this.account.shipping.city = 'Portland';
      this.account.shipping.state = 'OR';
      this.account.shipping.postalCode = '07000';
      this.account.shipping.validated = true;

      this.account.creditCard.cvv = '99';
      this.account.creditCard.name = 'joe joesphson';
      this.account.creditCard.expirationMonth = '8';
      this.account.creditCard.expirationYear = '2027';
      this.account.creditCard.number = '4001 3001 9999 1111';
      this.account.creditCard.cardType = 'Visa';
    }

    this.checkParameters();
  }

  checkParameters = (): void => {
    const pageId = this.route.snapshot.queryParamMap.get('page');
    if (!pageId) return;

    const index = this.pages.findIndex((x) => x.id == pageId);
    if (index == -1) return;

    this.selectedPage = index;
    this.selectedPageId = pageId;
  };

  loadAccountRegistrationDefaults = (countryCode: string): void => {
    this.spinnerService.show(
      this.translateService.instant('account-register.loading-details'),
      this.spinnerWrapper.nativeElement
    );

    this.interfaxService.getAccountRegistrationDefaults(countryCode, this.defaultsLoaded, this.loadFailed);
  };

  defaultsLoaded = (ard: AccountRegistrationDetails): void => {
    this.spinnerService.hide();

    this.account.countryCode = ard.countryCode;
    this.account.shipping.country = ard.countryCode;
    this.account.billing.country = ard.countryCode;

    //tbd: if (!this.account.inbound.countryCode) this.account.inbound.countryCode = ard.countryCode;

    const primaryPart = ard.outboundPricing.prePaidParts.find((ppp) => ppp.basePart.availableForNewUser);
    this.account.outboundType = primaryPart ? primaryPart.basePart.number : -1;

    this.accountRegistrationDefaults = ard;
    this.show = true;
  };

  loadFailed = (): void => {
    //tbd
  };

  onPageValid = (valid: boolean): void => {
    this.valid = valid;
  };

  addPage = (id: string): void => {
    this.pages.push(new WizardBarPage(id, this.translateService.instant(`account-register.wizard-page-${id}`)));
  };

  onBack = () => {
    this.selectedPage--;
    this.update();
    this.directiveRef.scrollToTop();
  };

  onNext = () => {
    if (this.selectedPageId == 'finish') return;
    if (this.selectedPageId == 'confirmation') {
      this.completeRegistration();
    } else {
      this.validatePage();
    }

    this.directiveRef.scrollToTop();
  };

  completeRegistration = () => {
    this.spinnerService.show(
      this.translateService.instant('account-register.complete-account-registration'),
      document.documentElement
    );

    this.interfaxService.completeRegistration(this.account, this.accountCreated, this.failed);
  };

  accountCreated = (result: ApiResult): void => {
    this.registrationResult = result;
    if (!this.registrationResult.success) {
      this.spinnerService.hide();
      this.errorsService.showErrorsModal(
        result.errors,
        this.translateService.instant('account-register.registration-failed')
      );
      return;
    }

    this.authService.login(
      this.account.username,
      this.account.password,
      this.sessionStorageService.getIsMobile(),
      this.onAuthorizeSuccess,
      this.onAuthorizeFailure
    );
  };

  onAuthorizeSuccess = (ar: AuthorizationResult) => {
    this.spinnerService.hide();

    if (!ar.succeeded) {
      this.toastr.error(ar.errorText);
      this.onFinalizing();
      return;
    }

    this.authService.setSession(ar);
    this.authService.shareSessionForTabs(60 * 1000);
    this.onFinalizing();
  };

  onAuthorizeFailure = (err) => {
    this.spinnerService.hide();
    Log.error('AccountCreateComponent.loginFailureCallBack', err);
    this.toastr.error(this.translateService.instant('Login.InvalidLoginData'));

    this.onFinalizing();
  };

  onFinalizing = (): void => {
    const redirectUrl = this.registrationResult.object?.redirectUrl || this.registrationResult.object?.nextStepRedirect;
    if (redirectUrl) {
      this.authService.shareSessionForTabs(60 * 1000);
      location = redirectUrl;
      return;
    }

    this.moveToNextPage();
  };

  validatePage = () => {
    const pageId = this.pages[this.selectedPage].id;

    if (pageId == 'billing-details' || pageId == 'payment') {
      this.validateAddresses(pageId);
    } else if (pageId == 'account-details') {
      this.validateAccountDetails();
    } else {
      this.moveToNextPage();
    }
  };

  validateAccountDetails = (): void => {
    this.spinnerService.show(
      this.translateService.instant('account-register.validating-account'),
      document.documentElement
    );

    this.interfaxService.validateRegistration(this.account, this.accountValid, this.failed);
  };

  accountValid = (): void => {
    this.spinnerService.hide();
    this.moveToNextPage();
  };

  moveToNextPage = (): void => {
    this.valid = false;
    this.selectedPage++;
    this.update();
  };

  validateAddresses = (pageId: string): void => {
    if (this.needsAddressValidation(pageId)) {
      const aa = new AccountAddresses(
        pageId == 'payment' ? this.account.billing : null,
        pageId == 'billing-details' ? this.account.shipping : null
      );

      this.spinnerService.show(
        this.translateService.instant('account-register.validating-address'),
        document.documentElement
      );

      this.interfaxService.validateRegistrationAddresses(
        this.account.countryCode,
        aa,
        this.addressesValidated,
        this.failed
      );
    } else {
      this.moveToNextPage();
    }
  };

  addressesValidated = (avr: AddressValidationResult): void => {
    this.spinnerService.hide();

    if (avr.billingUpdated || avr.shippingUpdated) {
      this.showAddressValidationModal(avr);
    } else {
      this.moveToNextPage();
    }
  };

  showAddressValidationModal = (avr: AddressValidationResult): void => {
    this.addressValidationResults = avr;
    this.ngxSmartModalService.getModal('addressRegistrationValidationModal').open();
  };

  rejectAddressValidationResults = (): void => {
    this.ngxSmartModalService.getModal('addressRegistrationValidationModal').close();
  };

  acceptAddressValidationResults = (): void => {
    this.ngxSmartModalService.getModal('addressRegistrationValidationModal').close();

    if (this.addressValidationResults.billingUpdated) {
      this.account.billing = this.addressValidationResults.addresses.billing;
    }

    if (this.addressValidationResults.shippingUpdated) {
      this.account.shipping = this.addressValidationResults.addresses.shipping;
    }

    this.moveToNextPage();
  };

  failed = (e: any) => {
    this.spinnerService.hide();
    this.errorsService.showErrorsModal(e, this.translateService.instant('account-register.validation-failed'));
    //todo: show error
  };

  needsAddressValidation = (pageId: string): boolean => {
    if (pageId == 'billing-details') return this.account.shipping.changed;

    if (this.account.billingSameAsShipping) return false;

    return this.account.billing.changed;
  };

  update = (): void => {
    this.nextButtonText = this.selectedPage == this.pages.length - 1 ? 'account-register.create-account' : 'App.next';
    this.selectedPageId = this.pages[this.selectedPage].id;
    this.cdr.detectChanges();
    this.directiveRef.update();
  };

  onCountryChanged = (countryCode: string): void => {
    this.loadAccountRegistrationDefaults(countryCode);
  };

  onCancel = () => {};
}
